import React, { Component } from "react";
import { connect } from "react-redux";
import { loadDashboardInfo } from "../../actions/rootActions";
import EditorContainer from "./EditorContainer";
import SwitchDashboard from "./SwitchDashboard";

class Editor extends Component {
  constructor(props) {
    super(props);
  }

  componentDidMount() {
    const { id } = this.props.match.params;
    this.props.loadDashboardInfo(id);
  }

  render() {
    return (
      <React.Fragment>
        <SwitchDashboard />
        <EditorContainer version="viewer" />
      </React.Fragment>
    );
  }
}

const mapDispatchToProps = dispatch => ({
  loadDashboardInfo: id => dispatch(loadDashboardInfo(id))
});

export default connect(
  null,
  mapDispatchToProps
)(Editor);
