import React, { Component } from "react";
import { connect } from "react-redux";
import { loadSavedQuery, changeChartTitle } from "../../actions/rootActions";
import Chart from "./Chart";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

class ChartContainer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: false
    };
  }

  componentDidMount() {
    if (this.props.savedQuery && (this.props.dryRun === false || this.props.version === 'viewer')) {
      this.props.loadSavedQuery(this.props.savedQuery, this.props.index);
      this.setState({
        loading: true
      });
    }
  }

  componentDidUpdate(prevProps) {
    if (prevProps.results !== this.props.results) {
      this.setState({
        loading: false
      });
    }
  }

  render() {
    const {
      chartTitle,
      index,
      type,
      legend,
      sparkline,
      stacking,
      results,
      error,
      isLoading
    } = this.props;
    const { loading } = this.state;
    return (
      <React.Fragment>
        <div className="chart-title">
          <input
            type="text"
            value={chartTitle}
            onChange={e => this.props.changeChartTitle(e.target.value, index)}
            placeholder="Enter your title..."
          />
        </div>
        <Chart
          type={type}
          {...this.props}
          legend={
            legend.value === false
              ? { show: false }
              : { position: legend.value }
          }
          sparkline={sparkline.value && sparkline.value}
          stacking={stacking.value && stacking.value}
          results={results && results}
        />
        {((!results && loading === false && isLoading === false) || error) && (
          <div className="new-chart-info">
            <div className="save-query-message">
              {error ? error : "Select saved queries for this chart..."}
            </div>
          </div>
        )}
        {(loading || isLoading === index) && !error && (
          <div className="new-chart-info">
            <span className="loading">
              <FontAwesomeIcon icon="spinner" />
            </span>
          </div>
        )}
      </React.Fragment>
    );
  }
}

const mapStateToProps = state => ({
  isLoading: state.editorReducer.isLoading,
  dryRun: state.editorReducer.dashboardInfo.dryRun
});

const mapDispatchToProps = dispatch => ({
  loadSavedQuery: (value, index) => dispatch(loadSavedQuery(value, index)),
  changeChartTitle: (title, index) => dispatch(changeChartTitle(title, index))
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(ChartContainer);
